body {
  margin: 0;
  padding: 0;
  background: #F2F4F5;
  color: #1E1E1F;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
}

#root {
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
}

.info {
  text-align: center;
  margin-top: 36px;
  font-size: 24px;
}

@media (min-width: 1024px) {
  #root {
    margin: 0 300px;
  }
}