form {
  width: 90%;
  max-width: 300px;
  height: 300px;
  background-color: #fff;
  padding: 0 40px;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
}

label {
  text-align: center;
  font-weight: bold;
  font-size: 1.4rem;
  margin-bottom: 26px;
  color: #1E1E1F;
}

textarea {
  background-color: #f9fbfc;
  border: 2px, solid #202329;
  border-radius: 2px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  color: #1e1e1f;
  font-size: 1rem;
  padding: 12px;
  height: 96px;
  width: calc(100%-25px);
}

textarea::placeholder {
  color: #a5a5a5;
  font-family: "Montserrat";
  font-weight: 400;
}

textarea:focus {
  outline-color: #61dafa;
}

.TodoForm-buttonContainer {
  margin-top: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.TodoForm-button {
  cursor: pointer;
  display: inline-block;
  font-size: 1.2rem;
  color: #202329;
  font-weight: 400;
  width: 120px;
  height: 48px;
  border-radius: 2px;
  border: none;
  font-family: "Montserrat";
}

.TodoForm-button-add {
  background-color: #61dafa;
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}

.TodoForm-button-add:hover {
    background-color: #53c3e2;
  }

.TodoForm-button-cancel {
  background: transparent;
}

@media (min-width: 1024px) {
    form {
        max-width: 800px;
    }
  }
